import React from 'react';
import "./Style.css"
import { Link } from "react-router-dom";


export default function Lists() {
  function changeBackground(e) {
    e.target.style.background = '#474B4F';
    e.target.style.color='white';
  }

  function getBackground(e) {
    e.target.style.background = 'yellow';
    e.target.style.color='black';
  }



  return (
    <>
      <h3>

        <div className='lists' >
          <Link to="About" style={{ color: 'white' }} onMouseOver={getBackground}     onMouseOut={changeBackground}>  About</Link>
        </div>
        <br/>
        <div className='lists' >
          <Link to="Qualifications"style={{ color: 'white' }} onMouseOver={getBackground}     onMouseOut={changeBackground}>  Qualifications</Link>
        </div>
        <br/>

        <div className='lists' >
          <Link to="Skills"style={{ color: 'white' }} onMouseOver={getBackground}     onMouseOut={changeBackground}>  Skills</Link>
        </div>
        <br/>
        <div className='lists' >
          <Link to="Projects"style={{ color: 'white' }} onMouseOver={getBackground}     onMouseOut={changeBackground}>  Projects</Link>
        </div>
        <br/>
        <div className='lists'   >
          <Link to="Experience"style={{ color: 'white' }}   onMouseOver={getBackground}     onMouseOut={changeBackground} >  Experience</Link>
        </div>
        <br/>
        


      </h3>













    </>
  );
}