import React from 'react';
import About from './Component2/About';
import Experience from './Component2/Experience';
import Skills from './Component2/Skills';
import Qualifications from './Component2/Qualifications';
import Projects from './Component2/Projects';
import { Routes, Route } from "react-router-dom";




export default function Descp() {

    return (
        <>
 
            <Routes>
            <Route path="" element={<About />} />
                <Route path="About" element={<About />} />
                <Route path="Experience" element={<Experience />} />
                <Route path="Skills" element={<Skills />} />
                <Route path="Projects" element={<Projects />} />
                <Route path="Qualifications" element={<Qualifications />} />

            </Routes>






           
        </>
    );
}